import { Link } from 'react-router-dom';
import "../styles/pages/Informatics.scss";
import Header from "../components/Header.jsx";
import InfoCard from "../components/InfoCard";
import PythonCard from "../components/PythonCard";
import PythonCardSwitcher from "../components/PythonCardSwitcher";


function Informatics() {
  return (
    <div className="container">
        <Header/>
<h2 data-aos="fade-down">    </h2>

    <div className="cards-grid">
      <InfoCard 
        icon="" 
        text=" Python     " 
        link="https://vkvideo.ru/playlist/-224816033_1" 
        animation="fade-right"
      />
      <InfoCard 
        icon="" 
        text="Stepik:  Python" 
        link="https://stepik.org/course/58852/info" 
        animation="zoom-in" 
        delay={100}
      />
      <InfoCard 
        icon="" 
        text=" HTML  CSS" 
        link="https://basicweb.ru" 
        animation="fade-left" 
        delay={200}
      />
    </div>
    <h2 data-aos="fade-down"> </h2>
    <img 
            src={`${process.env.PUBLIC_URL}/images/prog/1.PNG`} 
            alt=" " 
            className="table-img-inform"
    />
    <PythonCardSwitcher
  title="   "
  programs={[
    {
      title: "",
      code: `x = 777  # ,  
n = 5     #   
result = ''
while x > 0:
    result += str(x % n)
    x = x // n
result = result[::-1]`
    },
    {
      title: " 12- (  10 = A, 11 = B, .)",
      code: `x = 777 # ,  
n = 12 #   
result = ''
while x > 0 :
    if x%n == 10: result += 'A'
    elif x%n == 11: result += 'B'
    elif x%n == 12: result += 'C'
    else: result += str(x%n)
    x = x//n
result = result[::-1]`,
    },
    {
      title: " ",
      code: `a = bin(x)[2:] # 
b = oct(x)[2:] # 
c = int(x)     # 
d = hex(x)[2:] # `,
    },
  ]}
/>
 
    </div>
  );
}

export default Informatics;

 

